import React, { ChangeEvent, useState, useEffect, useCallback } from 'react';
import Button from '@mui/material/Button';
import Container from '@mui/material/Container';
import Typography from '@mui/material/Typography';
import TextField from '@mui/material/TextField';
import Checkbox from '@mui/material/Checkbox';
import { DataGrid } from '@mui/x-data-grid';

import { AppDef, AppParams } from '../apps';
import { version } from 'os';


type DataType = {
  name: string,
  base_experience: number,
  moves: Array< {
    move : {
      name: string,
    },
    version_group_details: Array<{
      level_learned_at: number,
      move_learn_method: { name: string },
      version_group: { name: string }
    }>,
  } >,
  sprites: { [key: string]: string },
  stats: Array< {
    base_stat: number,
    effort: number,
    stat: {
      name: string,
      url: string,
    }
  } >,
}

const appDef: AppDef = {
    id: 'pokemon',
    label: 'Pokemon',
    component: Pokemon,
}
export default appDef;

/**
 * Pokemon
 */
function Pokemon( params: AppParams ) {

  const [ isFetching, setIsFetching ] = useState<boolean>(false);

  /**
   * The name of the pokemon to retrieve
   */
  const [ pokemon, setPokemon ] = useState<string>('pikachu');

  const [ data, setData ] = useState<DataType|null>(null);

  console.log( 'Data', data );

  //
  // Callbacks
  //

  const fetchPokemon = useCallback( async () => {
    if ( ! isFetching ) {
      setIsFetching( true );
      try {
        const response = await fetch ("https://pokeapi.co/api/v2/pokemon/" + pokemon );
        const data = await response.json();

        console.log( 'Setting data', data );
        setData( data );

        const audio = new Audio( data.cries.latest );
        audio.play();

      } finally {
        setIsFetching( false );
      }
    }
  }, [ isFetching, pokemon ]);

  const columns = [
    { field: 'name', headerName: 'Stat', width: 200 },
    { field: 'base_stat', headerName: 'Base Stat' },
    { field: 'effort', headerName: 'Effort' },
  ];

  return (
    <Container>
      <Typography variant="h2" color="inherit" component="div">
      Pokemon
      </Typography>
      <Button variant='contained' onClick={ fetchPokemon }>Get Pokemon</Button>

      <hr/>

      <div>
        <TextField id="standard-basic" label="Pokemon Name" value={pokemon} onChange={ ev => setPokemon( ev.target.value) } variant="standard" />
      </div>
    
      <hr/>
      { data ? <div>
        {
          Object.entries( data.sprites ).filter( ([k,v]) => typeof v === 'string' ).map( ([sprite,url]) => 
            <img src={url} width="200" alt={sprite}/>
          )
        }
        <p/>
        <TextField InputProps={{ readOnly: true, }} label="Name" value={data.name} />
        <TextField InputProps={{ readOnly: true, }} label="Base Experience" value={data.base_experience} />
        <Typography variant="h3" color="inherit" component="div">Stats</Typography>
        <DataGrid
          rows={data.stats.map( (it,index) => {
            const { base_stat, effort, stat } = it;
            const { name } = stat;
            return {
              id: index,
              name,
              base_stat,
              effort,
            };
          } )}
          columns={columns}
          initialState={{
            pagination: {
              paginationModel: { page: 0, pageSize: 10 },
            },
          }}
          pageSizeOptions={[10, 10]}
        />
        <Typography variant="h3" color="inherit" component="div">Moves</Typography>
        <DataGrid
          rows={data.moves.map( (it,index) => {
            const { move, version_group_details } = it;
            const { name } = move;
            return {
              id: index,
              name,
              version_group_details: version_group_details.map( vgd =>
                `[${vgd.version_group.name}] Level ${vgd.level_learned_at}`
              ).join( '; ' )
            };
          } )}
          columns={[
            { field: 'name', headerName: 'Move', width: 200 },
            { field: 'version_group_details', headerName: 'Version/Group Details', width: 800 },
          ]}
          initialState={{
            pagination: {
              paginationModel: { page: 0, pageSize: 5 },
            },
          }}
          pageSizeOptions={[5, 10]}
        />
      </div> : null
      }
    </Container>
  );
} // END Pokemon
